/* src/components/ProfilePicture.css */
.profile-picture {
    text-align: center;
    margin: 2rem auto;
  }
  
  .picture-container {
    width: 400px; /* Container width */
    height: 400px; /* Container height */
    overflow: hidden; /* Crop the content outside the container */
    border-radius: 50%; /* Makes the container circular */
    margin: 0; 
    padding: 0;
    /* Adjust the position of the container */
    position: relative;
    top: 0;
    right: 0;
  }
  
  .picture {
    width: 100%; /* Image width to fill the container */
    height: auto;
    
  }